import React from 'react'
import { DBcontrol } from './DBcontrol';
import './popupAdd.css'

export default function PopupAdd(props) {
    console.log("Pop UP HEREE");
    let obj = new DBcontrol();
    function addItem() {
        let title = document.getElementById('title').value;
        let key = document.getElementById('key2').value;
        console.log(key);
        let descrptn = document.getElementById('description2').value;
        obj.addItem(title, key, descrptn);
        props.triggerPop();
    }
    return (
        <div className="bigContainer">
            <div className="popupTask">
                <div className="closebutn" onClick={props.triggerPop}><i className="fas fa-times"></i></div>
                <div className="taskTitle"> 
                    <input type="text" id="title" placeholder=" Write Title..."/>
                </div>

                <div className="taskTitle"> 
                    <input type="number" id="key2" autoComplete="off" placeholder=" Enter key eg. 12345 if not entered then default key will be used"/> 
                </div>
                
                <div className="description"> 
                    <textarea  id="description2" rows="5" placeholder=" Task Description...."></textarea> 
                </div>
                <div className="finalbtns">
                    <button style={{backgroundColor: "rgb(15, 209, 151)"}} onClick={addItem}>Save</button>
                    <button style={{backgroundColor: "rgb(182, 59, 59)"}} onClick={props.triggerPop}>Cancel</button>
                </div>
            </div>
        </div>
    )
}
